<template>
  <v-card>
    <v-card-title>
      Device Groups
      <v-spacer></v-spacer>

      <v-btn color="accent" @click="addGroupDialog=true">
        <v-icon>add</v-icon>Create Group
      </v-btn>
    </v-card-title>
    <v-list two-line subheader>
      <v-list-tile v-for="item in groups" :key="item.name" avatar @click="groupDetails(item.name)">
        <v-list-tile-avatar>
          <v-icon>folder_open</v-icon>
        </v-list-tile-avatar>

        <v-list-tile-content>
          <v-list-tile-title>{{ item.name }}</v-list-tile-title>
        </v-list-tile-content>
        <v-spacer></v-spacer>
        <v-btn
          v-if="item.name !== 'default'"
          flat
          color="red"
          v-on:click.stop="prepareDelete(item)"
        >Remove</v-btn>
      </v-list-tile>
      <div class="text-xs-center ma-1" v-if="loading">
        <v-progress-circular indeterminate color="primary"/>
      </div>
    </v-list>
    <v-dialog v-model="claimDeviceDialog" max-width="500">
      <v-card>
        <v-card-title class="headline">Claim Device Using Code</v-card-title>
        <v-card-text>
          <v-text-field v-model="claimCode" label="Claim Code"/>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click.native="claimDeviceDialog = false">Cancel</v-btn>
          <v-btn color="primary" flat @click.native="claimDevice()">Claim</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="addGroupDialog" max-width="500">
      <v-card>
        <v-card-title class="headline">Create Device Group</v-card-title>
        <v-card-text>
          <v-text-field v-model="groupName" label="Name"/>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click.native="addGroupDialog = false">Cancel</v-btn>
          <v-btn color="primary" flat @click.native="addGroup()">Add</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="removeGroupDialog" max-width="500">
      <v-card>
        <v-card-title>
          <span class="headline">Remove Group "{{groupToRemove.name}}" ?</span>
        </v-card-title>
        <v-card-actions>
          <v-btn flat @click="removeGroupDialog = false">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn flat color="red" @click="deleteGroup()">Remove</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar v-model="snackbar" :color="snackbarColor">
      {{ snackbarText }}
      <v-btn dark flat @click="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-card>
</template>

<script>
import axios from "@/axios";
import { auth } from "@/Auth";
import Device from "@/device";
import Group from "@/group";
import router from "@/router";

export default {
  components: { },
  data() {
    return {
      authenticated: auth.authenticated,
      selectedDevice: false,
      devices: [],
      groups: [],
      loading: false,
      groupToRemove: {},
      removeGroupDialog: false,
      claimDeviceDialog: false,
      claimingDevice: {},
      claimCode: "",
      selectedGroup: "",
      groupName: "",
      addGroupDialog: false,
      snackbar: false,
      snackbarText: "",
      snackbarColor: ""
    };
  },
  methods: {
    groupDetails(group) {
      router.push({ path: `/group/${group}/devices` });
    },
    prepareDelete(group) {
      this.groupToRemove = group;
      this.removeGroupDialog = true;
    },
    fetchDevices() {
      this.loading = true;
      axios
        .get("/api/devices")
        .then(response => {
          this.loading = false;
          this.devices = response.data.map(device => {
            Object.assign(device, {
              $socket: this.$socket,
              $options: this.$options,
              $set: this.$set
            });
            const d = new Device(device);
            d.subscribe();
            return d;
          });
        })
        .catch(() => {
          this.loading = false;
        });
    },
    fetchGroups() {
      this.loading = true;
      axios
        .get("/api/groups")
        .then(response => {
          this.loading = false;
          this.groups = response.data.map(group => {
            return new Group(group);
          });
        })
        .catch(() => {
          this.loading = false;
        });
    },
    unclaimDevice(item) {
      item.unclaim().then(() => {
        this.fetchDevices();
      });
    },
    addGroup() {
      const url = `/api/groups/create`;
      return axios
        .post(url, { groupname: this.groupName })
        .then(response => {
          if (response.data && response.data.error) {
            this.snackbar = true;
            this.snackbarColor = "error";
            this.snackbarText = response.data.error;
            this.addGroupDialog = false;
          } else {
            this.groupName = "";
            this.snackbar = true;
            this.snackbarColor = "success";
            this.snackbarText = "Group added successfully";
            this.addGroupDialog = false;
            this.fetchGroups();
          }
        })
        .catch(err => {
          console.warn(err);
        });
    },
    deleteGroup() {
      this.groupToRemove
        .delete()
        .then(response => {
          if (response.data && response.data.error) {
            this.removeGroupDialog = false;
            this.snackbar = true;
            this.snackbarColor = "error";
            this.snackbarText = response.data.error;
            this.groupToRemove = {};
          } else {
            this.removeGroupDialog = false;
            this.snackbar = true;
            this.snackbarColor = "success";
            this.snackbarText = "Group deleted";
            this.groupToRemove = {};
            this.fetchGroups();
          }
        })
        .catch(err => {
          this.snackbar = true;
          this.snackbarColor = "error";
          this.snackbarText = err;
          this.addAppDialog = false;
          this.groupToRemove = {};
          console.warn(err);
          this.resetAppToAdd();
        });
    },
    claimDevice() {
      const url = `/api/claim`;
      return axios
        .post(url, { claimCode: this.claimCode })
        .then(response => {
          if (response.data && response.data.error) {
            this.snackbar = true;
            this.snackbarColor = "error";
            this.snackbarText = response.data.error;
            this.claimDeviceDialog = false;
          } else {
            this.claimCode = "";
            this.snackbar = true;
            this.snackbarColor = "success";
            this.snackbarText = "Claimed device successfully";
            this.claimDeviceDialog = false;
            this.fetchDevices();
          }
        })
        .catch(err => {
          console.warn(err);
        });
    }
  },
  computed: {
    profile() {
      return auth.profile() || {};
    }
  },
  mounted() {
    this.fetchGroups();
  },
  watch: {}
};
</script>

