import Vue from 'vue'
import Router from 'vue-router'
import Groups from '@/components/Home'
import Login from '@/components/Login'
import Users from '@/components/Users'
import GroupDetails from '@/components/GroupDetails'
import Api from '@/components/Api'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Groups',
      component: Groups
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/users',
      name: 'Users',
      component: Users
    },
    {
      path: '/api',
      name: 'Api',
      component: Api
    },
    {
      path: '/group/:groupname/:tab',
      name: 'Group Details',
      component: GroupDetails
    }

  ]
})
