<template>
  <div>
    <v-tabs v-model="tab" centered color="primary" slider-color="secondary">
      <v-tab
        :key="`devices`"
        :to="{ name: 'Group Details', params: { groupname: this.$route.params.groupname, tab: 'devices'}}"
        :href="`#devices`"
      >Devices</v-tab>
      <v-tab
        :key="`apps`"
        :to="{ name: 'Group Details', params: { groupname: this.$route.params.groupname, tab: 'apps'}}"
        :href="`#apps`"
      >Apps</v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item :key="`devices`" :value="`devices`">
        <v-card>
          <v-card-title :primary-title="true">
            Devices
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="devices"
            :search="search"
            :loading="devicesloading"
          >
            <template v-slot:items="props">
              <td>{{ props.item.identity }}</td>
              <td>{{ props.item.online ? "online" : "offline" }}</td>
              <td>{{ props.item.lastip || "-" }}</td>
              <td>{{ (new Date(props.item.lastseen/1000)).toLocaleString() }}</td>
            </template>
            <v-alert
              v-slot:no-results
              :value="true"
              color="error"
              icon="warning"
            >Your search for "{{ search }}" found no results.</v-alert>
          </v-data-table>
        </v-card>
      </v-tab-item>
      <v-tab-item :key="`apps`" :value="`apps`">
        <v-card>
          <v-card-title>
            Apps
            <v-spacer></v-spacer>
            <v-btn color="accent" @click="addAppDialog=true">
              <v-icon>cloud</v-icon>Connect App
            </v-btn>
          </v-card-title>
          <v-list two-line subheader>
            <template v-for="(item, index) in apps">
              <v-list-tile :key="item.aid">
                <v-list-tile-avatar>
                  <v-icon v-if="item.connected" color="green lighten-1">cloud</v-icon>

                  <v-icon v-else color="red darken-2">cloud</v-icon>
                </v-list-tile-avatar>

                <v-list-tile-content>
                  <v-list-tile-title>{{ item.name }}</v-list-tile-title>
                </v-list-tile-content>
                <v-spacer></v-spacer>
                <v-btn flat color="red" @click="prepareDelete(item)">Remove</v-btn>
              </v-list-tile>
              <v-divider v-if="index + 1 < apps.length" :key="`divider-${index}`"></v-divider>
            </template>
            <div
              class="subtitle px-3"
              v-if="!appsloading && apps.length === 0"
            >You have no connected apps.</div>
            <div class="text-xs-center ma-1" v-if="appsloading">
              <v-progress-circular indeterminate color="primary"/>
            </div>
          </v-list>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
    <v-dialog v-model="removeAppDialog" max-width="500">
      <v-card>
        <v-card-title>
          <span class="headline">Remove Application "{{appToRemove.name}}" ?</span>
        </v-card-title>
        <v-card-actions>
          <v-btn flat @click="removeAppDialog = false">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn flat color="red" @click="deleteApp()">Remove</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="addAppDialog" max-width="500">
      <v-card>
        <v-card-title>
          <span class="headline">Connect App</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="appToAdd.name"
                  label="Name*"
                  :rules="[v => !!v || 'Name is required']"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-select
                  v-model="appToAdd.type"
                  :items="['Murano', 'External HTTPS']"
                  label="Type*"
                  required
                ></v-select>
              </v-flex>
              <v-flex v-if="appToAdd.type == 'External HTTPS'" xs12>
                <v-text-field
                  v-model="appToAdd.httpsTarget"
                  :rules="[v => /^https:\/\/.+/.test(v) || 'Must start with https://']"
                  label="HTTPS callback url"
                  required
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat @click="addAppDialog = false">Cancel</v-btn>
          <v-btn color="primary" @click.native="connectApp()">Generate Token</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar" :color="snackbarColor">
      {{ snackbarText }}
      <v-btn dark flat @click="snackbar = false">Close</v-btn>
    </v-snackbar>
    <v-dialog v-model="appTokenDialog" width="700">
      <v-card color="secondary" class="white--text">
        <v-card-title primary-title>
          <div>
            <div class="headline">App token</div>
            <span>{{ appToken }}</span>
            <input type="hidden" id="app-token" :value="appToken">
          </div>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat dark @click="CopyToClipboard()">Copy to Clipboard</v-btn>
          <v-btn flat dark @click="appTokenDialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "@/axios";
import Group from "@/group";

export default {
  data() {
    return {
      devicesloading: false,
      appsloading: false,
      search: "",
      tabstate: "",
      addAppDialog: false,
      removeAppDialog: false,
      appToRemove: {},
      appTokenDialog: false,
      appToken: "",
      appToAdd: {
        name: "",
        type: "Murano",
        muranoTarget: "",
        httpsTarget: ""
      },
      headers: [
        {
          text: "Device ID",
          sortable: false,
          value: "id"
        },
        { text: "Status", value: "online", sortable: false },
        { text: "Last IP", value: "lastip", sortable: false },
        { text: "Last Seen", value: "update", sortable: false }
      ],
      devices: [],
      apps: [],
      snackbar: false,
      snackbarText: "",
      snackbarColor: ""
    };
  },
  props: {
    applinked: Number,
    deviceclaimed: Number
  },
  methods: {
    CopyToClipboard() {
      let testingCodeToCopy = document.querySelector("#app-token");
      testingCodeToCopy.setAttribute("type", "text");
      testingCodeToCopy.select();
      document.execCommand("copy");
      testingCodeToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },
    prepareDelete(app) {
      this.appToRemove = app;
      this.removeAppDialog = true;
    },
    fetchDevices() {
      this.devicesloading = "primary";
      this.group.devices
        .then(devices => {
          this.devices = devices;
          this.devicesloading = false;
        })
        .catch(() => {
          this.devicesloading = false;
        });
    },
    fetchApps() {
      this.appsloading = true;
      this.group.apps
        .then(apps => {
          this.apps = apps;
          this.appsloading = false;
        })
        .catch(() => {
          this.appsloading = false;
        });
    },
    resetAppToAdd() {
      this.appToAdd.name = "";
      this.appToAdd.target = "Murano";
      this.appToAdd.muranoTarget = "";
      this.appToAdd.httpsTarget = "";
    },
    connectApp() {
      const url = `/api/app/${this.group.name}/create`;
      const appdata = {
        name: this.appToAdd.name,
        type: this.appToAdd.type,
        target:
          this.appToAdd.type == "Murano"
            ? this.appToAdd.muranoTarget
            : this.appToAdd.httpsTarget
      };
      return axios
        .post(url, appdata)
        .then(response => {
          if (response.data && response.data.error) {
            this.snackbar = true;
            this.snackbarColor = "error";
            this.snackbarText = response.data.error;
            this.addAppDialog = false;
            this.resetAppToAdd();
          } else {
            this.addAppDialog = false;
            this.resetAppToAdd();
            this.appToken = response.data.token;
            this.appTokenDialog = true;
            this.fetchApps();
          }
        })
        .catch(err => {
          this.snackbar = true;
          this.snackbarColor = "error";
          this.snackbarText = err;
          this.addAppDialog = false;
          console.warn(err);
          this.resetAppToAdd();
        });
    },
    deleteApp() {
      this.appToRemove
        .delete()
        .then(response => {
          if (response.data && response.data.error) {
            this.snackbar = true;
            this.snackbarColor = "error";
            this.snackbarText = response.data.error;
            this.removeAppDialog = false;
            this.appToRemove = {};
          } else {
            this.removeAppDialog = false;
            this.snackbar = true;
            this.snackbarColor = "success";
            this.snackbarText = "Application removed";
            this.appToRemove = {};
            this.fetchApps();
          }
        })
        .catch(err => {
          this.snackbar = true;
          this.snackbarColor = "error";
          this.snackbarText = err;
          this.addAppDialog = false;
          console.warn(err);
          this.resetAppToAdd();
        });
    }
  },
  watch: {
    applinked: function() {
      this.fetchApps();
    },
    deviceclaimed: function() {
      this.fetchDevices();
    }
  },
  computed: {
    group() {
      return new Group({ name: this.$route.params.groupname });
    },
    tab: {
      get: function() {
        this.tabstate = this.$route.params.tab === "apps" ? "apps" : "devices";
        return this.tabstate;
      },
      set: function(newvalue) {
        this.tabstate = newvalue;
      }
    }
  },
  mounted() {
    this.fetchDevices();
    this.fetchApps();
  }
};
</script>

<style>
.api-result {
  background-color: #777;
  color: white;
}

.border-right {
  border-right: 1px solid rgba(0, 0, 0, 0.12);
}
</style>
