// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './app'
import router from './router'
import Vuetify from 'vuetify'
import axios from "@/axios"
import vueHeadful from 'vue-headful'
import 'material-design-icons-iconfont/dist/material-design-icons.css' // Ensure you are using css-loader
import 'vuetify/dist/vuetify.min.css'
//import VueNativeSock from 'vue-native-websocket'

Vue.use(Vuetify, {
    theme: {
      primary: '#FFFFFF', // Display white before loading the branding.json
      secondary: '#FFFFFF', // Display white before loading the branding.json
      accent: '#F15C2E',
      error: '#FF5252',
      info: '#2196F3',
      success: '#4CAF50',
      warning: '#FFC107'
    },
    options: {
      customProperties: true
    },
    iconfont: 'md'
})

//Vue.use(VueNativeSock, 'ws://localhost:3000/api/subscribe')
Vue.use(require('vue-moment'))
Vue.component('vue-headful', vueHeadful)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App v-bind:title="title" v-bind:description="description" />',
  data: function(){
    return {
      title: 'OEM Product',
      description: 'OEM Product'
    }
  },
  created() {
    // Loading branding data
    axios.get('static/branding.json')
      .then(({data}) => {
        if (typeof data !== 'object') {
          return console.error('Wrong branding.json format, json expected.')
        }
        const {title, description, theme = {}} = data
        if (title) this.title = title
        if (description) this.description = description
        Object.assign(this.$vuetify.theme, theme)
      }, error => console.error(`Unable to load branding.json file`, error));
  }
})
