<template>
  <v-container class="login-container" fill-height fluid>
    <v-layout class="center">
      <v-flex md4 offset-md4 sm6 offset-sm3 xs8 offset-xs2>
        <div class="text-xs-center">
          <img src="static/brandlogo.png" class="login-logo mt-2 mb-2">
        </div>
        <div class="subheading text-sm-center" v-if="isClaimQuery()">
          <v-card color="secondary" flat class="white--text">
            <v-card-title primary-title>
              <div>
                <span>Please create an account or login to an existing account to claim your device(s).</span>
              </div>
            </v-card-title>
          </v-card>
        </div>
        <div class="subheading text-sm-center" v-if="isAppQuery()">
          <v-card color="secondary" flat class="white--text">
            <v-card-title primary-title>
              <div>
                <span>To complete linking your application with this product, please create an account or login with an existing account.</span>
              </div>
            </v-card-title>
          </v-card>
        </div>
        <v-tabs v-model="active" slider-color="accent" fixed-tabs color="secondary">
          <v-tab key="login" ripple class="white--text">Login</v-tab>
          <v-tab key="signup" ripple class="white--text">Signup</v-tab>

          <v-tab-item key="login" class="width-limit py-5">
            <v-form ref="login" v-model="loginValid">
              <v-text-field dark required :rules="emailRules" label="Email" v-model="email"/>
              <v-text-field
                dark
                required
                :rules="passwordRules"
                type="password"
                label="Password"
                v-model="password"
              />
            </v-form>
            <div class="text-xs-center">
              <v-btn :disabled="!loginValid" @click="login()" color="primary">Login</v-btn>
            </div>
          </v-tab-item>

          <v-tab-item key="signup" class="width-limit py-4">
            <v-form ref="signup" v-model="signupValid">
              <v-text-field dark required :rules="nameRules" label="Name" v-model="name"/>
              <v-text-field dark required :rules="emailRules" label="Email" v-model="email"/>
              <v-text-field
                dark
                required
                :rules="passwordRules"
                type="password"
                label="Password"
                v-model="password"
              />
            </v-form>
            <div class="text-xs-center">
              <v-btn :disabled="!signupValid" @click="signup()" color="primary">Signup</v-btn>
            </div>
          </v-tab-item>
        </v-tabs>
      </v-flex>
    </v-layout>
    <v-snackbar v-model="snackbar" :color="snackbarColor">
      {{ snackbarText }}
      <v-btn dark flat @click="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
import { auth } from "@/Auth";
import router from "@/router";

export default {
  data() {
    return {
      active: "login",
      signupValid: false,
      loginValid: false,
      name: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 30) || "Name must be less than 30 characters"
      ],
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid"
      ],
      password: "",
      passwordRules: [
        v => !!v || "Password is required",
        v => v.length > 7 || "Password too short"
      ],
      snackbar: false,
      snackbarText: "",
      snackbarColor: ""
    };
  },
  methods: {
    isClaimQuery() {
      return this.$route.query.claim;
    },
    isAppQuery() {
      return this.$route.query.apptoken;
    },
    redirectHome() {
      router.replace({ path: "/", query: this.$route.query });
    },
    signup() {
      const data = {
        name: this.name,
        email: this.email,
        password: this.password
      };
      auth
        .signup(data)
        .then(() => {
          this.redirectHome();
        })
        .catch(err => {
          console.warn({ err });
          this.snackbar = true;
          this.snackbarText = "Error signing up";
          this.snackbarColor = "error";
        });
    },
    login() {
      const data = {
        email: this.email,
        password: this.password
      };
      auth
        .login(data)
        .then(() => {
          this.redirectHome();
        })
        .catch(err => {
          console.warn({ err });
          this.snackbar = true;
          this.snackbarText = "Invalid Login";
          this.snackbarColor = "error";
        });
    }
  }
};
</script>

<style>
.login-logo {
  max-width:100%;
  max-height:100%;
}
.login-container {
  background-color: var(--v-secondary-base);
}

.width-limit {
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}
</style>
