import _ from 'lodash'
import axios from '@/axios'
import Device from '@/device'
import App from '@/app'

export default class Group {
  constructor(data) {
    Object.assign(this, data)
  }

  delete() {
    const url = `/api/groups/delete`
    return axios.post(url, this)
  }

  get devices() {
    const url = `/api/groups/${this.name}/devices`
    return axios.get(url).then(response => {
      let devices = Array.isArray(response.data) ? response.data : []
      return _.map(devices, function (device) {
        return new Device(device)
      });
    })
  }

  get apps() {
    const url = `/api/groups/${this.name}/apps`
    const group = this.name
    return axios.get(url).then(response => {
      let apps = Array.isArray(response.data) ? response.data : []
      return _.map(apps, function (app) {
        app.group = group
        return new App(app)
      });
    })
  }
}
